import interfaces from '../../utils/interfaces.js';
	import {
	        mapState,
	        mapMutations
	    } from 'vuex';
export default {
computed: {
            ...mapState(['type'])
        },
	data() {
		return {
			
			list: [],
			categoryName:"",
			tabCur: 0,
			mainCur: 0,
			height:0,
			verticalNavTop: 0,
			cid:-1,
			TwoList:[],
			hasNextPage:true
		};
	},
	onLoad() {
		var _this=this;
		           // 获取手机状态栏高度
		           uni.getSystemInfo({
		               success:function(data){
		                   // 将其赋值给this
		                   _this.height=data.statusBarHeight;
		               }
		           });
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
		
		
		this.request({
							url: interfaces.getoneCategory,
		/* 					data:{sessionKey:sessionKey}, */
							success: ((res) => {
							
								this.list=res.data;
								this.getTwoCategory(this.list[0].cId);	
								if(this.type=="c"){
									this.categoryName=this.list[0].cName;
								}else{
									this.categoryName=this.list[0].cEname;
								}
								
							
							})
						});
		
		
		
	},
	onReady() {
		uni.hideLoading()
	},
	
	methods: {
		into(id){
			var pages = getCurrentPages();
			var prevPage = pages[pages.length - 2]; 
			prevPage.onShow(id);
			uni.navigateBack({
				
			});
		},
		getData(){
			
				this.request({
									url: interfaces.getoneCategory,
				/* 					data:{sessionKey:sessionKey}, */
									success: ((res) => {
										console.log(res);
										this.list=res.data;
											
									
									
									})
								});
			
		},
		
		
		getTwoCategory(id){
			
			this.request({
								url: interfaces.getTwoCategory,
			 					data:{pid:id}, 
								success: ((res) => {
									console.log(res);
									this.TwoList=res.data;
										
								
								
								})
							});
		},
		
		TabSelect(e,name) {
			
			this.tabCur = e.currentTarget.dataset.id;
			this.getTwoCategory(e.target.id);
			this.categoryName=name;
			this.cid=e.target.id;
			
			
		},
		VerticalMain(e) {



			let that = this;
			let tabHeight = 0;
			if (this.load) {
				for (let i = 0; i < this.list.length; i++) {
					let view = uni.createSelectorQuery().select("#main-" + this.list[i].cId);
					view.fields({
						size: true
					}, data => {
						this.list[i].top = tabHeight;
						tabHeight = tabHeight + data.height;
						this.list[i].bottom = tabHeight;
					}).exec();
				}
				this.load = false
			}
			let scrollTop = e.detail.scrollTop + 10;
			for (let i = 0; i < this.list.length; i++) {
				if (scrollTop > this.list[i].top && scrollTop < this.list[i].bottom) {
					this.verticalNavTop = (this.list[i].id - 1) * 50
					this.tabCur = this.list[i].id
					console.log(scrollTop)
					return false
				}
			}
		},
		
	


	},
}
